@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

p {
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.user {
  cursor: pointer;
  width: 320px;
  padding: 5px;
  margin: 20px auto;
  display: flex;
  justify-content: space-between;
  border: 1px solid #bbc0c7;
  border-radius: 10px;
}
.user__position {
  height: 60px;
  line-height: 60px;
  vertical-align: middle;
  font-size: 20px;
  font-weight: 700;
}
.user__row {
  display: flex;
  justify-content: space-between;
}
.user__time--penalty {
  width: 200px;
}
.user__row--small {
  width: 150px;
  display: flex;
  justify-content: space-between;
}
.user__thumbnail {
  margin-right: 10px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid #fff;
}
.user__active {
  box-shadow: 0 0 0 2pt #7743ce;
}
.user__name {
  width: 200px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 20px;
  font-weight: bold;
}
.user__time {
  color: #d56cf0;
}
.user__speed {
  position: relative;
  color: #81b6f7;
}
.user__speed::after {
  content: "";
  position: absolute;
  top: 0;
  left: -10px;
  display: block;
  width: 1px;
  height: 100%;
  border: 1px solid #81b6f7;
  background-color: #81b6f7;
}

